  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25;
  color: #00449e;
  box-sizing: border-box;
}

.modal__close {
  position: absolute;
  right: 15px;
  top: 20px;
  background: transparent;
  border: 0;
}

