/*--------------------main-slider----------------------*/
.main-slider {
	position: relative;
	.wrapper {
		position: relative;
	}
	.swiper {
		@include max-screen($md){
			margin-left: -12px;
			margin-right: -12px;
			
		}
		@include max-screen($sm){
			margin-left: -24px;
			margin-right: -24px;
			
		}
		&:after{
			content: '';
			display: block;
			position: absolute;
			right: -2px;
			top: 0;
			height: 100%;
			width: 83px;
			background-image: url(../img/gradient-line.svg);
			background-position: right 0;
			background-repeat: repeat-y;
			background-size: contain;
			z-index: 2;

			@include max-screen($sm){
				display: none;
			}
		}
	}
	.swiper-wrapper {

	}
	.swiper-button-prev,
	.swiper-button-next{
		display: none;
	}
	.swiper-pagination{
		position: static;
		margin-top: 12px;

		@include max-screen($sm){
			display: none;
		}
	}
	
}
.swiper-container {}
.main-slider__slider {}
.swiper-wrapper {}
.swiper-slide {}
.main-slider__slide {
	overflow: hidden;
	// padding-bottom: prc(288, 940);
	padding-bottom: 29.79%;
	border-radius: 4px;	

	@include max-screen($sm){
		padding-bottom: prc(149, 320);
		// height: 148px;
	}
}
._fit {}
._fit-img {}
.swiper-pagination {}
.slider-nav {
	position: absolute;
	top: 50%;
	width: 24px;
	height: 24px;
	margin-top: -12px;
	cursor: pointer;
	z-index: 2;
	transition: transform .4s;

	@media (any-hover: hover) { //только для устройств с ховером
			&:hover{
				transform: scale(2);
			}
		}

	&.swiper-button-disabled{
		opacity: .5;
	}
	.icon{
		@include absolute-center-old;
		font-size: 6.5px;
		color: $text-l;
	}
}
.slider-nav--prev{
	display: none;
	left: 0;
	@include min-screen($sm){
		display: block;
		
	}
	@include min-screen($max){
		left: -32px;
		
	}
	.icon{
		transform: rotate(90deg);
	}
}
.slider-nav--next {
	display: none;
	right: 0;
	@include min-screen($sm){
		display: block;
		
	}
	@include min-screen($max){
		right: -32px;
		
	}
	.icon{
		transform: rotate(-90deg);
	}
}
/*--------------------END main-slider----------------------*/ 